import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';

class App3 extends Component {
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">API 1 demo</h1>
        </header>
      </div>
    );
  }
}

export default App3;



